import React from 'react'

export default function ContactMeSection() {
  return (
    <section className="contact-me  observer-section" id="contact-me">
        <div className="contact-me__wrapper">
        <h2 className="second-title"> </h2>

        <form action="" className="contact-me__form">
            <div className="input-wrapper left">
            <label className="label bold" htmlFor="name"></label>
            <input className="input" type="text" id="name" placeholder="  " required />
            </div>
            <div className="input-wrapper right">
            <label className="label bold" htmlFor="email">E-mail</label>
            <input className="input" type="email" id="email" placeholder="  e-mail " required />
            </div>

            <div className="cls"></div>

            <div className="textarea-wrapper">
            <label className="label bold" htmlFor="message"></label>
            <textarea className="textarea" name="message" placeholder=" " id="message" cols="30" rows="10" required></textarea>
            </div>

            <button type="submit" className="submit"></button>
        </form>
        </div>
    </section>
  )
}
